.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 12%;
  margin: 0%;
  background: #2c4359;
  display:flex;
  justify-content: center;
  align-items: center;
}

.socialMedia svg {
  color: white;
  margin: 20px;
  font-size: 70px;
  cursor: pointer;
}

.footer p {
  color: white;
}

@media only screen and (max-width: 600px) {
  .footer svg {
    font-size: 50px;
  }
}
